.App {
  font-family: sans-serif;
  text-align: center;
}
.search-container {
  width: 280px;
  display: flex;
  flex-direction: column;
  margin-left: 100px;
}
.dropdown {
  width: 220px;
  background-color: white;
  display: flex;
  flex-direction: column;
  border: 1px solid gray;
}
.dropdown:empty {
  border: none;
}
.dropdown-row {
  cursor: pointer;
  text-align: start;
  margin: 2px 0;
}
.search-input {
  width: 220px;
}
.search-inner {
  display: flex;
}
